* {
  box-sizing: border-box;
  cursor: none;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
}

.custom-cursor {
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: all .1s;
  position: fixed;
}

.custom-cursor.hover {
  mix-blend-mode: normal;
  background: linear-gradient(135deg, #ef4444 0, #dc2626 100%);
  width: 40px;
  height: 40px;
}

.custom-cursor.click {
  background: linear-gradient(135deg, #10b981 0, #059669 100%);
  transform: scale(.8);
}

.btn, .project-card, .service-card, .skill-item, .social-link, a, button {
  cursor: none;
}

.custom-cursor:before {
  content: "";
  background: inherit;
  border-radius: inherit;
  opacity: .3;
  width: 100%;
  height: 100%;
  transition: all .2s;
  position: absolute;
  transform: scale(1.5);
}

.custom-cursor.hover:before {
  opacity: .1;
  transform: scale(2);
}

html {
  scroll-behavior: smooth;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

html::-webkit-scrollbar {
  display: none;
}

body {
  color: #e2e8f0;
  touch-action: pan-y;
  -webkit-overflow-scrolling: touch;
  background-color: #0a0a0a;
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, sans-serif;
  font-weight: 400;
  line-height: 1.7;
}

.stylish-scroll-bar {
  z-index: 10000;
  backdrop-filter: blur(10px);
  background: #ffffff1a;
  border-bottom: 1px solid #ffffff1a;
  width: 100%;
  height: 6px;
  position: fixed;
  top: 0;
  left: 0;
}

.scroll-progress {
  background: linear-gradient(90deg, #3b82f6, #8b5cf6, #06b6d4, #3b82f6) 0 0 / 300% 100%;
  border-radius: 0 3px 3px 0;
  width: 0%;
  height: 100%;
  transition: width .1s;
  animation: 4s ease-in-out infinite gradient-shift;
  position: relative;
  overflow: hidden;
}

.scroll-progress:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff6, #0000);
  width: 100%;
  height: 100%;
  animation: 2s infinite shimmer;
  position: absolute;
  top: 0;
  left: -100%;
}

.scroll-progress:after {
  content: "";
  background: linear-gradient(90deg, #0000, #fff9);
  border-radius: 0 3px 3px 0;
  width: 20px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
}

.hero {
  color: #f8fafc;
  background: linear-gradient(135deg, #0a0a0a 0, #1a1a1a 30%, #0f0f0f 70%, #0a0a0a 100%);
  align-items: center;
  min-height: 100vh;
  padding: 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero:before {
  content: "";
  background: radial-gradient(circle at 20% 80%, #3b82f626 0, #0000 50%), radial-gradient(circle at 80% 20%, #8b5cf626 0, #0000 50%), radial-gradient(circle at 40% 40%, #06b6d41a 0, #0000 50%);
  animation: 6s ease-in-out infinite float;
  position: absolute;
  inset: 0;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0)rotate(0);
  }

  33% {
    transform: translateY(-20px)rotate(1deg);
  }

  66% {
    transform: translateY(10px)rotate(-1deg);
  }
}

.hero:after {
  content: "";
  opacity: .1;
  background-image: linear-gradient(90deg, #0000 98%, #3b82f64d 100%), linear-gradient(0deg, #0000 98%, #3b82f64d 100%);
  background-size: 50px 50px;
  animation: 20s linear infinite gridMove;
  position: absolute;
  inset: 0;
}

@keyframes gridMove {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(50px, 50px);
  }
}

.hero-image {
  z-index: 2;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.profile-image {
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  border-radius: 50%;
  width: 300px;
  height: 300px;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 0 20px 60px #0006, 0 0 0 1px #ffffff1a;
}

.profile-image:before {
  content: "";
  z-index: -1;
  background: linear-gradient(45deg, #3b82f6, #8b5cf6, #06b6d4, #3b82f6) 0 0 / 400% 400%;
  border-radius: 50%;
  animation: 3s infinite gradient-shift;
  position: absolute;
  inset: -2px;
}

.profile-img {
  object-fit: cover;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
}

.profile-image:hover {
  transform: scale(1.05);
  box-shadow: 0 30px 80px #3b82f666, 0 0 0 1px #fff3;
}

.profile-image:hover .profile-img {
  transform: scale(1.1);
}

.hero-container {
  z-index: 2;
  justify-content: center;
  align-items: center;
  max-width: 1400px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  position: relative;
}

.hero-content {
  text-align: center;
  z-index: 3;
  max-width: 800px;
}

.hero-badge {
  color: #3b82f6;
  backdrop-filter: blur(10px);
  opacity: 1;
  background: linear-gradient(135deg, #3b82f61a, #8b5cf61a);
  border: 1px solid #3b82f64d;
  border-radius: 50px;
  margin-bottom: 2rem;
  padding: .5rem 1.5rem;
  font-size: .9rem;
  font-weight: 600;
  animation: 1s ease-out .2s forwards slideInUp;
  display: inline-block;
  transform: translateY(0);
}

.hero-title {
  letter-spacing: -.025em;
  -webkit-text-fill-color: transparent;
  opacity: 1;
  background: linear-gradient(135deg, #f8fafc 0, #cbd5e1 50%, #94a3b8 100%) text;
  margin-bottom: 1rem;
  font-size: 5rem;
  font-weight: 900;
  line-height: 1.1;
  animation: none;
  position: relative;
  transform: translateY(0);
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.highlight {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 50%, #06b6d4 100%) text;
  display: inline-block;
  position: relative;
}

.highlight:after {
  content: "";
  opacity: .3;
  z-index: -1;
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  border-radius: 4px;
  width: 100%;
  height: 8px;
  animation: 1s ease-out 1.2s forwards expandWidth;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.hero-subtitle {
  color: #94a3b8;
  letter-spacing: .025em;
  opacity: 1;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
  font-size: 1.75rem;
  font-weight: 500;
  animation: 1s ease-out .6s forwards slideInUp;
  display: flex;
  transform: translateY(0);
}

.hero-subtitle span {
  backdrop-filter: blur(10px);
  background: linear-gradient(135deg, #3b82f61a, #8b5cf61a);
  border: 1px solid #3b82f633;
  border-radius: 25px;
  padding: .5rem 1rem;
  transition: all .3s;
}

.hero-subtitle span:hover {
  background: linear-gradient(135deg, #3b82f633, #8b5cf633);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px #3b82f64d;
}

.hero-description {
  color: #94a3b8;
  opacity: 1;
  max-width: 700px;
  margin-bottom: 3.5rem;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.25rem;
  line-height: 1.8;
  animation: 1s ease-out .8s forwards slideInUp;
  position: relative;
  transform: translateY(0);
}

.hero-description:before {
  content: "";
  background: linear-gradient(90deg, #3b82f6, #8b5cf6);
  border-radius: 1px;
  width: 60px;
  height: 2px;
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.hero-buttons {
  opacity: 1;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  animation: 1s ease-out 1s forwards slideInUp;
  display: flex;
  transform: translateY(0);
}

.btn {
  cursor: pointer;
  letter-spacing: .025em;
  backdrop-filter: blur(10px);
  border: none;
  border-radius: 16px;
  padding: 18px 36px;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn-primary {
  color: #fff;
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  position: relative;
  box-shadow: 0 8px 30px #3b82f64d;
}

.btn-primary:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.btn-primary:hover:before {
  left: 100%;
}

.btn-primary:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 40px #3b82f666;
}

.btn-secondary {
  color: #f8fafc;
  backdrop-filter: blur(10px);
  background: #ffffff0d;
  border: 2px solid #3b82f64d;
  position: relative;
}

.btn-secondary:before {
  content: "";
  opacity: 0;
  z-index: -1;
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  width: 100%;
  height: 100%;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.btn-secondary:hover:before {
  opacity: 1;
}

.btn-secondary:hover {
  color: #fff;
  border-color: #0000;
  transform: translateY(-4px);
  box-shadow: 0 12px 40px #3b82f64d;
}

.hero-floating {
  pointer-events: none;
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.floating-element {
  opacity: .6;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  border-radius: 50%;
  width: 6px;
  height: 6px;
  animation: 8s ease-in-out infinite floatElement;
  position: absolute;
}

.floating-element:first-child {
  animation-delay: 0s;
  top: 20%;
  left: 10%;
}

.floating-element:nth-child(2) {
  animation-delay: 2s;
  top: 60%;
  left: 85%;
}

.floating-element:nth-child(3) {
  animation-delay: 4s;
  top: 80%;
  left: 20%;
}

.floating-element:nth-child(4) {
  animation-delay: 6s;
  top: 30%;
  left: 80%;
}

@keyframes floatElement {
  0%, 100% {
    opacity: .6;
    transform: translateY(0)scale(1);
  }

  50% {
    opacity: 1;
    transform: translateY(-20px)scale(1.2);
  }
}

section {
  padding: 120px 0;
}

.section-title {
  text-align: center;
  color: #f8fafc;
  letter-spacing: -.025em;
  margin-bottom: 4rem;
  font-size: 3.5rem;
  font-weight: 900;
  position: relative;
}

.section-title:after {
  content: "";
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  border-radius: 2px;
  width: 80px;
  height: 4px;
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.about {
  background: #0f0f0f;
  padding: 60px 0;
}

.about-content {
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
}

.about-text p {
  color: #94a3b8;
  max-width: 700px;
  margin-bottom: 2rem;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.25rem;
  line-height: 1.8;
}

.about-stats {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 3rem;
  margin-top: 4rem;
  display: grid;
}

.stat {
  text-align: center;
  opacity: 0;
  filter: blur(5px);
  background: linear-gradient(135deg, #1a1a1a 0, #262626 100%);
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  padding: 2rem;
  transition: all 1.2s cubic-bezier(.4, 0, .2, 1);
  transform: scale(.3)translateY(100px);
  box-shadow: 0 4px 20px #0000004d;
}

.stat.animate {
  opacity: 1;
  filter: blur();
  transform: scale(1)translateY(0);
}

.stat:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px #0006;
}

.stat h3 {
  color: #3b82f6;
  letter-spacing: -.025em;
  opacity: 0;
  margin-bottom: .5rem;
  font-size: 3rem;
  font-weight: 900;
  transition: all .6s .3s;
  transform: translateY(20px);
}

.stat.animate h3 {
  opacity: 1;
  transform: translateY(0);
}

.stat p {
  color: #94a3b8;
  opacity: 0;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all .6s .5s;
  transform: translateY(20px);
}

.stat.animate p {
  opacity: 1;
  transform: translateY(0);
}

.services {
  background: #0f0f0f;
}

.services-grid {
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  display: grid;
}

.service-card {
  opacity: 0;
  filter: blur(5px);
  background: linear-gradient(135deg, #3b82f61a 0, #8b5cf61a 100%);
  border: 1px solid #3b82f633;
  border-radius: 20px;
  padding: 2rem;
  transition: all 1.2s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
  transform: scale(.3)translateY(100px);
}

.service-card.animate {
  opacity: 1;
  filter: blur();
  transform: scale(1)translateY(0);
}

.service-card:before {
  content: "";
  background: linear-gradient(90deg, #3b82f6, #8b5cf6, #06b6d4) 0 0 / 300% 100%;
  height: 3px;
  animation: 3s infinite gradient-shift;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.service-card:hover {
  border-color: #3b82f666;
  transform: translateY(-8px);
  box-shadow: 0 20px 60px #3b82f633;
}

.service-icon {
  color: #fff;
  opacity: 0;
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  margin-bottom: 1.5rem;
  font-size: 2rem;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  transform: scale(.8);
  box-shadow: 0 8px 25px #3b82f64d;
}

.service-card.animate .service-icon {
  opacity: 1;
  transform: scale(1);
}

.service-icon:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.service-card:hover .service-icon:before {
  left: 100%;
}

.service-card:hover .service-icon {
  transform: scale(1.1)rotate(5deg);
  box-shadow: 0 12px 35px #3b82f666;
}

.service-content h3 {
  color: #f8fafc;
  letter-spacing: -.025em;
  opacity: 0;
  margin-bottom: .75rem;
  font-size: 1.5rem;
  font-weight: 700;
  transition: all .6s .3s;
  transform: translateY(20px);
}

.service-card.animate .service-content h3 {
  opacity: 1;
  transform: translateY(0);
}

.service-content p {
  color: #94a3b8;
  opacity: 0;
  margin-bottom: 1.5rem;
  font-size: .95rem;
  line-height: 1.6;
  transition: all .6s .5s;
  transform: translateY(20px);
}

.service-card.animate .service-content p {
  opacity: 1;
  transform: translateY(0);
}

.service-features {
  opacity: 0;
  flex-wrap: wrap;
  gap: .5rem;
  transition: all .8s .7s;
  display: flex;
  transform: scale(.5)translateY(30px);
}

.service-card.animate .service-features {
  opacity: 1;
  transform: scale(1)translateY(0);
}

.service-features span {
  color: #3b82f6;
  opacity: 0;
  background: #3b82f626;
  border: 1px solid #3b82f64d;
  border-radius: 20px;
  padding: .4rem .8rem;
  font-size: .8rem;
  font-weight: 600;
  transition: all .3s;
  transform: scale(.8);
}

.service-card.animate .service-features span {
  opacity: 1;
  transform: scale(1);
}

.service-features span:first-child, .service-card.animate .service-features span:first-child {
  transition-delay: .8s;
}

.service-features span:nth-child(2), .service-card.animate .service-features span:nth-child(2) {
  transition-delay: .9s;
}

.service-features span:nth-child(3), .service-card.animate .service-features span:nth-child(3) {
  transition-delay: 1s;
}

.service-features span:nth-child(4), .service-card.animate .service-features span:nth-child(4) {
  transition-delay: 1.1s;
}

.service-features span:nth-child(5), .service-card.animate .service-features span:nth-child(5) {
  transition-delay: 1.2s;
}

.service-features span:nth-child(6), .service-card.animate .service-features span:nth-child(6) {
  transition-delay: 1.3s;
}

.service-features span:hover {
  background: #3b82f640;
  border-color: #3b82f680;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px #3b82f64d;
}

.education {
  background: linear-gradient(135deg, #0a0a0a 0, #1a1a1a 100%);
}

.education-status {
  color: #10b981;
  background: #10b9811a;
  border: 1px solid #10b9814d;
  border-radius: 12px;
  margin-top: .5rem;
  padding: .25rem .75rem;
  font-size: .9rem;
  font-weight: 600;
  display: inline-block;
}

.experience {
  background: #0f0f0f;
}

.experience-timeline {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}

.experience-timeline:before {
  content: "";
  background: linear-gradient(135deg, #3b82f6 0, #8b5cf6 100%);
  width: 2px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.experience-item {
  opacity: 0;
  filter: blur(5px);
  align-items: center;
  margin-bottom: 3rem;
  transition: all 1.2s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
  transform: scale(.3)translateY(100px);
}

.experience-item.animate {
  opacity: 1;
  filter: blur();
  transform: scale(1)translateY(0);
}

.experience-item:nth-child(odd) {
  flex-direction: row;
}

.experience-item:nth-child(2n) {
  flex-direction: row-reverse;
}

.experience-period {
  color: #fff;
  letter-spacing: .025em;
  z-index: 2;
  text-align: center;
  background: linear-gradient(135deg, #10b981 0, #059669 100%);
  border-radius: 50px;
  min-width: 150px;
  padding: 1rem 2rem;
  font-size: .95rem;
  font-weight: 700;
  transition: all .5s;
  position: relative;
  transform: scale(.8);
  box-shadow: 0 8px 25px #10b9814d;
}

.experience-item.animate .experience-period {
  transform: scale(1.2);
  box-shadow: 0 15px 40px #10b98180;
}

.experience-content {
  opacity: .5;
  background: #1a1a1a;
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  flex: 1;
  margin: 0 2rem;
  padding: 2rem;
  transition: all .8s cubic-bezier(.4, 0, .2, 1);
  transform: scale(.7);
  box-shadow: 0 8px 40px #0000004d;
}

.experience-item.animate .experience-content {
  opacity: 1;
  border-color: #10b98166;
  transform: scale(1);
  box-shadow: 0 20px 60px #00000080;
}

.experience-content h3 {
  color: #f8fafc;
  letter-spacing: -.025em;
  opacity: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
  transition: all .6s .3s;
  transform: translateY(20px);
}

.experience-item.animate .experience-content h3 {
  opacity: 1;
  transform: translateY(0);
}

.experience-company {
  color: #10b981;
  opacity: 0;
  margin-bottom: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all .6s .5s;
  transform: translateY(20px);
}

.experience-item.animate .experience-company {
  opacity: 1;
  transform: translateY(0);
}

.experience-description {
  color: #94a3b8;
  opacity: 0;
  margin-bottom: 1.5rem;
  font-size: 1rem;
  line-height: 1.6;
  transition: all .6s .7s;
  transform: translateY(20px);
}

.experience-item.animate .experience-description {
  opacity: 1;
  transform: translateY(0);
}

.experience-tech {
  opacity: 0;
  flex-wrap: wrap;
  gap: .5rem;
  transition: all .8s .9s;
  display: flex;
